<template>
  <div>
    <div class="app-container-item">
      <div class="app-form" v-show="showQuery">
        <div class="app-form-item">
          <el-input type="text" v-model="tableOptions.query.name" placeholder="输入权限名称搜索" style="width: 200px;"/>
        </div>
        <div class="app-form-item">
          <el-select v-model="tableOptions.query.status" placeholder="请选择状态" clearable style="width: 120px;">
            <el-option
              v-for="item in statusTypes"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </div>
        <div class="app-form-item">
          <el-select v-model="tableOptions.query.type" placeholder="请选择权限类型" clearable style="width: 220px;">
            <el-option
              v-for="item in typeTypes"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </div>
        <div class="app-form-item">
          <el-select v-model="tableOptions.query.systemId"    placeholder="请选择所属系统" clearable style="width: 200px;">
            <el-option
              v-for="item in systemMap"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
        </div>
        <div class="app-form-buttons">
          <div class="app-form-buttons-item">
            <el-button @click="loadData" type="primary">
              <ant-icon icon-class="SearchOutlined"></ant-icon>
              查询
            </el-button>
          </div>
          <div class="app-form-buttons-item">
            <el-button @click="clear" type="success">
              <ant-icon icon-class="RedoOutlined"></ant-icon>
              重置
            </el-button>
          </div>
        </div>
      </div>
      <div class="app-hr" v-show="showQuery"></div>
      <div class="app-form">
        <div class="app-form-buttons">
          <div class="app-form-buttons-item" v-buttonshow="'upms:Permission:add'">
            <el-button @click="addPermission" type="success">
              <ant-icon icon-class="PlusOutlined"></ant-icon>
              新增
            </el-button>
          </div>
          <div class="app-form-buttons-item" v-buttonshow="'upms:Permission:upd'">
            <el-button @click="editPermissionButton" type="danger">
              <ant-icon icon-class="EditOutlined"></ant-icon>
              修改
            </el-button>
          </div>
          <div class="app-form-buttons-item" v-buttonshow="'upms:Permission:del'">
            <el-button @click="delPermissionBeforeSystemButton" type="warning">
              <ant-icon icon-class="DeleteOutlined"></ant-icon>
              删除
            </el-button>
          </div>
          <div class="app-form-buttons-item" v-buttonshow="'upms:Permission:auth'">
            <el-button @click="empowerPermissionButton" type="warning">
              <ant-icon icon-class="UploadOutlined"></ant-icon>
              授权
            </el-button>
          </div>
          <div class="app-form-buttons-item" v-buttonshow="'upms:Permission:enable'">
            <el-button @click="openPermissionButton" type="warning">
              <ant-icon icon-class="UploadOutlined"></ant-icon>
              启用
            </el-button>
          </div>
          <div class="app-form-buttons-item" v-buttonshow="'upms:Permission:unable'">
            <el-button @click="closePermissionButton" type="warning">
              <ant-icon icon-class="UploadOutlined"></ant-icon>
              禁用
            </el-button>
          </div>
          <div class="app-form-buttons-item">
            <el-button type="warning">
              <ant-icon icon-class="UploadOutlined"></ant-icon>
              导出
            </el-button>
          </div>
        </div>
        <el-button-group class="app-form-button-group align-right">
          <el-button
            plain
            type="info"
            icon="el-icon-search"
            @click="toggleQuery"
          />
          <el-button
            icon="el-icon-refresh"
            @click="refresh"
          />
        </el-button-group>
      </div>
    </div>
    <div class="app-container-item">
      <TableBase ref="tableref" v-bind="tableOptions"></TableBase>
    </div>
    <el-dialog
      title="权限编辑"
      :visible.sync="centerDialogVisible"
      width="480px"
      class="app-dialog-big"
    >
      <el-form :model="permissionObj" :rules="rules" label-suffix="" ref="ruleForm" label-width="120px"
               class="demo-ruleForm">
        <el-form-item label="菜单名称" prop="name">
          <el-input v-model="permissionObj.name" placeholder="权限名称"></el-input>
        </el-form-item>
        <el-form-item label="图标" prop="icon">
          <el-input v-model="permissionObj.icon" placeholder="图标"></el-input>
        </el-form-item>
        <el-form-item label="排序" prop="orders">
          <el-input-number v-model="permissionObj.orders" :min="1"></el-input-number>
        </el-form-item>
        <el-form-item label="权限值" prop="permissionValue">
          <el-input v-model="permissionObj.permissionValue" placeholder="权限值"></el-input>
        </el-form-item>
        <!--<el-form-item label="状态" prop="status">-->
        <!--<el-radio v-model="permissionObj.status" :label="'0'">正常</el-radio>-->
        <!--<el-radio v-model="permissionObj.status" :label="'1'">禁用</el-radio>-->
        <!--</el-form-item>-->
        <el-form-item label="所属系统" prop="systemId">
          <el-select v-model="permissionObj.systemId" :disabled="objStatus" @change="changeSystem" placeholder="请选择">
            <el-option
              v-for="item in systemMap"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="父级" prop="pid">
          <el-cascader
            v-model="permissionObj.pid"
            :options="permissionTree"
            :props="{ checkStrictly: true, label: 'name',value: 'id', children: 'children',emitPath: false, expandTrigger: 'hover'}"
            clearable></el-cascader>
        </el-form-item>
        <el-form-item label="所用资源" prop="permissionInterfacePoListId">
          <el-select  collapse-tags v-model="permissionObj.permissionInterfacePoListId" multiple placeholder="请选择">
            <el-option
              v-for="item in interfaceList"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="菜单类型" prop="type">
          <el-radio v-model="permissionObj.type" :label="'1'">目录</el-radio>
          <el-radio v-model="permissionObj.type" :label="'2'">菜单</el-radio>
          <el-radio v-model="permissionObj.type" :label="'3'">按钮</el-radio>
        </el-form-item>
        <el-form-item label="路由地址" prop="path">
          <el-input v-model="permissionObj.path" placeholder="路由地址"></el-input>
        </el-form-item>
        <el-form-item label="组件名称" prop="component">
          <el-input v-model="permissionObj.component" placeholder="组件名称"></el-input>
        </el-form-item>
        <el-form-item label="菜单状态" prop="visible">
          <el-radio v-model="permissionObj.visible" :label="'1'">隐藏</el-radio>
          <el-radio v-model="permissionObj.visible" :label="'0'">显示</el-radio>
        </el-form-item>
        <el-form-item label="layer组件" prop="isLayer">
          <el-radio v-model="permissionObj.isLayer" :label="'0'">是</el-radio>
          <el-radio v-model="permissionObj.isLayer" :label="'1'">否</el-radio>
        </el-form-item>
        <el-form-item label="主页面" prop="isMain">
          <el-radio v-model="permissionObj.isMain" :label="'0'">是</el-radio>
          <el-radio v-model="permissionObj.isMain" :label="'1'">否</el-radio>
        </el-form-item>
        <el-form-item label="是否外链接" prop="isFrame">
          <el-radio v-model="permissionObj.isFrame" :label="'0'">是</el-radio>
          <el-radio v-model="permissionObj.isFrame" :label="'1'">否</el-radio>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="centerDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="savePermissionObj">确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog
      title="权限编辑"
      :visible.sync="powerDialogVisible"
      width="480px"
    >
      <el-form :model="permissionObj" :rules="rules" label-suffix="" ref="ruleForm" label-width="120px"
               class="demo-ruleForm">
        <el-form-item label="菜单名称" prop="name">
          <el-input v-model="permissionObj.name" placeholder="权限名称"></el-input>
        </el-form-item>
        <el-form-item label="所属系统" prop="systemId">
          <el-select v-model="permissionObj.systemId" :disabled="powerDialogVisible" @change="changeSystem" placeholder="请选择">
            <el-option
              v-for="item in systemMap"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="所用资源" prop="permissionInterfacePoListId">
          <el-select  collapse-tags v-model="permissionObj.permissionInterfacePoListId" multiple placeholder="请选择">
            <el-option
              v-for="item in interfaceList"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="powerDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="savePermissionObj">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
// import
import TableBase from '@/components/table/TableBase'
import {
  addPermission,
  delPermission, editPermission, permissionOpenOrClose,
  queryInterface,
  queryPermission,
  queryPermissionInterface,
  queryPermissionTree,
  querySystem
} from '@/api'

export default {
  name: 'Permission',
  data () {
    return {
      // 定义的每个data 属性必须有注释
      // 注释规则： //
      // 查询是否显示
      showQuery: true,
      typeTypes: [{
        value: '1',
        label: '目录'
      }, {
        value: '2',
        label: '菜单'
      }, {
        value: '3',
        label: '按钮'
      }],
      statusTypes: [{
        value: '0',
        label: '正常'
      }, {
        value: '1',
        label: '禁用'
      }, {
        value: '2',
        label: '系统禁用'
      }],
      // 是否为修改状态
      objStatus: true,
      // 所有系统Map
      systemMap: {},
      // 权限树
      permissionTree: [],
      // 填写验证
      rules: {
        pid: [{required: true, message: '父级不能为空', trigger: 'blur'}],
        name: [{required: true, message: '菜单名称不能为空', trigger: 'blur'}],
        orders: [{required: true, message: '排序不能为空', trigger: 'blur'}],
        systemId: [{required: true, message: '所属系统不能为空', trigger: 'blur'}],
        type: [{required: true, message: '类型不能为空', trigger: 'blur'}],
        path: [{required: true, message: '路由地址不能为空', trigger: 'blur'}],
        component: [{required: true, message: '组件名称不能为空', trigger: 'blur'}],
        visible: [{required: true, message: '菜单状态不能为空', trigger: 'blur'}],
        isLayer: [{required: true, message: 'layer组件不能为空', trigger: 'blur'}],
        isMain: [{required: true, message: '主页面不能为空', trigger: 'blur'}],
        isFrame: [{required: true, message: '是否外链接不能为空', trigger: 'blur'}]
      },
      // 权限对象
      permissionObj: {},
      interfaceList: [],
      // 编辑弹框显示隐藏
      centerDialogVisible: false,
      // 编辑弹框显示隐藏
      powerDialogVisible: false,
      query: {
        name: ''
      },
      tableOptions: {
        api: queryPermission,
        hideOnSinglePage: false,
        query: {
          name: ''
        },
        staticData: [],
        columns: [
          {
            attr: {
              prop: 'checkbox',
              type: 'selection',
              label: '多选',
              'width': '80'
            }
          },
          {
            attr: {
              type: 'index',
              label: '序号',
              width: '80'
            }
          },
          {
            attr: {
              prop: 'name',
              label: '权限名称',
              'min-width': '140',
              'show-overflow-tooltip': true
            }
          },
          // {
          //   attr: {
          //     prop: 'permissionId',
          //     label: '所属权限'
          //   }
          // },
          {
            attr: {
              prop: 'systemId',
              label: '所属系统',
              'min-width': '180',
              'show-overflow-tooltip': true,
              formatter: (row, column, cellValue, index) => {
                let val = ''
                if (this.systemMap[cellValue] !== null && this.systemMap[cellValue] !== undefined) {
                  val = this.systemMap[cellValue]['name']
                }
                return val
              }
            }
          },
          {
            attr: {
              prop: 'path',
              label: '路由地址',
              'min-width': '180',
              'show-overflow-tooltip': true
            }
          },
          {
            attr: {
              prop: 'status',
              label: '状态',
              'min-width': '180',
              'show-overflow-tooltip': true,
              formatter (row, column, cellValue, index) {
                let val = '正常'
                if (cellValue === '1') {
                  val = '禁用'
                } else if (cellValue === '2') {
                  val = '系统禁用'
                }
                return val
              }
            }
          },
          {
            attr: {
              prop: 'type',
              label: '类型',
              'min-width': '180',
              'show-overflow-tooltip': true,
              formatter (row, column, cellValue, index) {
                let val = '目录'
                if (cellValue === '2') {
                  val = '菜单'
                } else if (cellValue === '3') {
                  val = '按钮'
                }
                return val
              }
            }
          },
          {
            attr: {
              prop: 'visible',
              label: '菜单状态',
              'min-width': '180',
              'show-overflow-tooltip': true,
              formatter (row, column, cellValue, index) {
                let val = '显示'
                if (cellValue === '1') {
                  val = '隐藏'
                }
                return val
              }
            }
          },
          {
            attr: {
              prop: 'isFrame',
              label: '是否为外链',
              'min-width': '120',
              'show-overflow-tooltip': true,
              formatter (row, column, cellValue, index) {
                let val = '否'
                if (cellValue === '0') {
                  val = '是'
                }
                return val
              }
            }
          },
          {
            attr: {
              prop: 'operation',
              label: '操作',
              width: 100,
              fixed: 'right'
            },
            template: [
              {
                el: 'el-button',
                attr: {
                  type: 'text'
                },
                attrByRow: {
                  disabled: function (index, row) {
                    if (row.status === '1' || row.status === '2') {
                      return true
                    }
                  }
                },
                content: '修改',
                handle: (index, row) => {
                  this.editPermissionRadio(row)
                }
              },
              {
                el: 'el-button',
                attr: {
                  type: 'text'
                },
                content: '删除',
                handle: (index, row) => {
                  this.delPermissionBeforeRadio(row)
                }
              }
            ]
          }
        ]
      }
    }
  },
  props: {
    // id: {
    //   default: '',
    //   type: String
    // }
  },
  components: {TableBase},
  // 页面创建加载方法
  beforeRouteEnter (to, from, next) {
    next(vm => {
      vm.selectSystem()
      vm.loadData()
    })
  },
  // beforeRouteUpdate (to, from, next) {
  // },
  // 页面销毁加载方法
  beforeRouteLeave (to, from, next) {
    next()
  },
  beforeCreate () {
  },
  created () {
  },
  beforeMount () {
  },
  mounted () {
  },
  beforeUpdate () {
  },
  updated () {
  },
  beforeDestroy () {
  },
  destroyed () {
  },
  watch: {},
  computed: {},
  directives: {},
  filter: {},
  methods: {
    // 方法调用不需要注释，方法定义需要注释
    /**
     * @desc 控制查询的显隐
     */
    toggleQuery () {
      this.showQuery = !this.showQuery
    },
    /**
     * @desc 刷新表格
     */
    refresh () {
      this.$refs.tableref.refresh()
    },
    clear () {
      this.tableOptions.query = {}
    },
    openPermissionButton () {
      let list = this.$refs.tableref.getSelectData()
      if (list.length === 0) {
        this.$message({
          showClose: true,
          message: '请选择要启用的系统！',
          type: 'warning'
        })
        return
      }
      for (let index = 0; index < list.length; index++) {
        if (list[index].status === '0') {
          this.$message({
            showClose: true,
            message: '权限名称为 ' + list[index].name + ' 正常，请不要重复启用！',
            type: 'warning'
          })
          return
        } else if (list[index].status === '2' || this.systemMap[list[index].systemId].status !== '0') {
          this.$message({
            showClose: true,
            message: '权限名称为 ' + list[index].name + ' 所属系统已禁用，请重新选择！',
            type: 'warning'
          })
          return
        }
      }

      this.$confirm('此操作将启用选择系统, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        permissionOpenOrClose({status: '1', listPermissionPo: list}).then((response) => {
          if (response.data.code === 0) {
            this.$message({
              showClose: true,
              message: '启用成功',
              type: 'success'
            })
            this.loadData()
          } else {
            this.$message({
              showClose: true,
              message: response.data.message,
              type: 'error'
            })
          }
        }).catch((response) => {
        })
      })
    },
    closePermissionButton () {
      let list = this.$refs.tableref.getSelectData()
      if (list.length === 0) {
        this.$message({
          showClose: true,
          message: '请选择要禁用的记录！',
          type: 'warning'
        })
        return
      }
      for (let index = 0; index < list.length; index++) {
        if (list[index].status !== '0') {
          this.$message({
            showClose: true,
            message: '权限名称为 ' + list[index].name + ' 已禁用，请不要重复禁用！',
            type: 'warning'
          })
          return
        }
      }
      this.$confirm('此操作将禁用选择权限, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        permissionOpenOrClose({status: '1', listPermissionPo: list}).then((response) => {
          if (response.data.code === 0) {
            this.$message({
              showClose: true,
              message: '禁用成功',
              type: 'success'
            })
            this.loadData()
          } else {
            this.$message({
              showClose: true,
              message: response.data.message,
              type: 'error'
            })
          }
        }).catch((response) => {
        })
      })
    },
    // 方法注释规则 /** */
    loadData () {
      this.$refs.tableref.refresh()
    },
    // 查询资源与权限关联
    selectPermissionInterface (permissionId) {
      this.permissionObj.permissionInterfacePoListId = []
      queryPermissionInterface({permissionId: permissionId}).then((response) => {
        if (response.data.code === 0) {
          response.data.data.forEach((interFaceObj) => {
            this.permissionObj.permissionInterfacePoListId.push(interFaceObj.interfaceId)
          })
        } else {
          this.$message({
            showClose: true,
            message: response.data.message,
            type: 'error'
          })
        }
      }).catch((response) => {
      })
    },
    selectSystem () {
      querySystem({}).then((response) => {
        if (response.data.code === 0) {
          response.data.data.forEach((systemObj) => {
            this.$set(this.systemMap, systemObj.id, systemObj)
            // this.systemMap[systemObj.id] = systemObj
          })
        } else {
          this.$message({
            showClose: true,
            message: response.data.message,
            type: 'error'
          })
        }
      }).catch((response) => {
        console.log(response)
      })
    },
    changeSystem (systemId) {
      this.selectInterface(systemId)
      this.selectPerTree(systemId)
    },
    selectPerTree (systemId) {
      queryPermissionTree({systemId: systemId, havePid: true}).then((response) => {
        if (response.data.code === 0) {
          this.permissionTree = response.data.data
        } else {
          this.$message({
            showClose: true,
            message: response.data.message,
            type: 'error'
          })
        }
      }).catch((response) => {
      })
    },
    selectInterface (systemId) {
      queryInterface({systemId: systemId}).then((response) => {
        if (response.data.code === 0) {
          this.interfaceList = response.data.data
          if (this.objStatus) {
            this.selectPermissionInterface(this.permissionObj.id)
          }
        } else {
          this.$message({
            showClose: true,
            message: response.data.message,
            type: 'error'
          })
        }
      }).catch((response) => {
      })
    },
    /**
     * 增加权限
     */
    addPermission () {
      //  设置新增默认值
      this.permissionObj = {
        status: '0',
        orders: 1,
        type: '2',
        visible: '0',
        isFrame: '1',
        isLayer: '1',
        isMain: '1',
        permissionInterfacePoListId: []
      }
      this.objStatus = false
      this.permissionTree = []
      this.interfaceList = []
      this.centerDialogVisible = true
    },
    /**
     * 修改权限
     */
    editPermissionRadio (perObj) {
      this.permissionObj = perObj
      this.objStatus = true
      this.selectInterface(this.permissionObj.systemId)
      this.selectPerTree(this.permissionObj.systemId)
      this.centerDialogVisible = true
    },

    /**
     * 授权系统
     */
    empowerPermissionButton () {
      let list = this.$refs.tableref.getSelectData()
      if (list.length === 0) {
        this.$message({
          showClose: true,
          message: '请选择要授权的记录！',
          type: 'warning'
        })
        return
      }
      if (list.length > 1) {
        this.$message({
          showClose: true,
          message: '授权只可选择一条记录！',
          type: 'warning'
        })
        return
      }
      for (let index = 0; index < list.length; index++) {
        if (list[index].status !== '0') {
          this.$message({
            showClose: true,
            message: '请先启用' + list[index].name + '，再修改！',
            type: 'warning'
          })
          return
        }
      }
      this.objStatus = true
      this.permissionObj = list[0]
      this.selectInterface(this.permissionObj.systemId)
      this.selectPerTree(this.permissionObj.systemId)
      this.powerDialogVisible = true
    },
    /**
     * 修改系统
     */
    editPermissionButton () {
      let list = this.$refs.tableref.getSelectData()
      if (list.length === 0) {
        this.$message({
          showClose: true,
          message: '请选择要修改的记录！',
          type: 'warning'
        })
        return
      }
      if (list.length > 1) {
        this.$message({
          showClose: true,
          message: '修改只可选择一条记录！',
          type: 'warning'
        })
        return
      }
      for (let index = 0; index < list.length; index++) {
        if (list[index].status !== '0') {
          this.$message({
            showClose: true,
            message: '请先启用' + list[index].name + '，再修改！',
            type: 'warning'
          })
          return
        }
      }
      this.objStatus = true
      this.permissionObj = list[0]
      this.selectInterface(this.permissionObj.systemId)
      this.selectPerTree(this.permissionObj.systemId)
      this.centerDialogVisible = true
    },
    delPermissionBeforeRadio (row) {
      this.$confirm('此操作将永久删除选择记录, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let list = []
        list.push(row)
        this.deletePermission(list)
      })
    },
    /**
     * 删除系统
     */
    delPermissionBeforeSystemButton () {
      let list = this.$refs.tableref.getSelectData()
      if (list.length === 0) {
        this.$message({
          showClose: true,
          message: '请选择要修改的记录！',
          type: 'warning'
        })
        return
      }
      this.$confirm('此操作将永久删除选择记录, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.deletePermission(list)
      })
    },
    deletePermission (list) {
      delPermission({listPermissionPo: list}).then((response) => {
        if (response.data.code === 0) {
          this.$message({
            showClose: true,
            message: '删除成功！',
            type: 'success'
          })
          this.centerDialogVisible = false
          this.loadData()
        } else {
          this.$message({
            showClose: true,
            message: response.data.message,
            type: 'error'
          })
        }
      }).catch((response) => {
        console.log(response)
        this.$message({
          showClose: true,
          message: '删除失败！',
          type: 'error'
        })
      })
    },
    /**
     * 提交权限对象
     */
    savePermissionObj () {
      this.$refs.ruleForm.validate((valid) => {
        if (!valid) {
          return false
        }
        const loadObj = this.$loading({
          lock: true,
          text: '正在加载...',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        })
        if (this.objStatus) {
          editPermission(this.permissionObj).then((response) => {
            loadObj.close()
            if (response.data.code === 0) {
              this.$message({
                showClose: true,
                message: '保存成功！',
                type: 'success'
              })
              this.centerDialogVisible = false
              this.powerDialogVisible = false
              this.loadData()
            } else {
              this.$message({
                showClose: true,
                message: response.data.message,
                type: 'error'
              })
            }
          }).catch((response) => {
            console.log(response)
            loadObj.close()
            this.$message({
              showClose: true,
              message: '保存失败！',
              type: 'error'
            })
          })
        } else {
          addPermission(this.permissionObj).then((response) => {
            loadObj.close()
            if (response.data.code === 0) {
              this.$message({
                showClose: true,
                message: '保存成功！',
                type: 'success'
              })
              this.centerDialogVisible = false
              this.loadData()
            } else {
              this.$message({
                showClose: true,
                message: response.data.message,
                type: 'error'
              })
            }
          }).catch((response) => {
            console.log(response)
            loadObj.close()
            this.$message({
              showClose: true,
              message: '保存失败！',
              type: 'error'
            })
          })
        }
      })
    }
  }
}
</script>

